"use client";

import { useEffect, useState } from "react";
import { useBooks } from "@/contexts/BooksContext";
import { useAuth } from "@/contexts/AuthCotext";
import ProtectedRoute from "@/app/components/ProtectedRoute";

export default function LivrosPage() {
  const { books, fetchBooks, addFavorite } = useBooks();
  const { user } = useAuth();
  const [query, setQuery] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  const handleSearch = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError("");
    try {
      await fetchBooks(query);
    } catch (err) {
      console.error(err);
      setError("Não foi possível buscar os livros.");
    } finally {
      setLoading(false);
    }
  };

  const handleFavorite = (book: any) => {
    addFavorite(book);
  };

  return (
    <ProtectedRoute>
      <div className="p-4">
        <h1 className="text-xl font-bold mb-4">Buscar Livros</h1>

        <form onSubmit={handleSearch} className="flex gap-2 mb-6">
          <input
            className="border rounded p-2 flex-1"
            placeholder="Digite o nome do livro"
            value={query}
            onChange={(e) => setQuery(e.target.value)}
          />
          <button className="bg-blue-600 text-white px-4 py-2 rounded">Buscar</button>
        </form>

        {loading && <p>Carregando livros...</p>}
        {error && <p className="text-red-600">{error}</p>}

        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          {books.map((book) => (
            <div key={book.id} className="p-4 border rounded shadow">
              {book.cover && <img src={book.cover} alt={book.title} className="mb-2" />}
              <h2 className="font-bold">{book.title}</h2>
              <p className="text-sm">{book.author}</p>
              <button
                onClick={() => handleFavorite(book)}
                className="bg-green-600 text-white px-3 py-1 mt-2 rounded"
              >
                Favoritar
              </button>
            </div>
          ))}
        </div>
      </div>
    </ProtectedRoute>
  );
}
